<template lang="html">
  <div class="play-round">
    <div class="questionArea">
        <h3>{{ round.question }}</h3>
    </div>
    <div class="timerArea">
      <p>{{ round.timer }} seconds remaining</p>
    </div>
    <div class="answerArea">
      <form @submit.prevent="round.compareAnswers()" autocomplete="off">
        <div class="field is-grouped">
          <p class="control is-expanded">
            <input class="input is-large is-rounded" type="text" name="answer" v-model="round.userAnswer">
          </p>
          <p class="control">
            <button type="button"
                    class="button is-rounded is-primary is-large"
                    @click="round.compareAnswers()"
                    :disabled="isDisabled"
                    name="button">
                    Submit
            </button>
          </p>
        </div>
      </form>
    </div>
    <div class="resultArea">
      <p v-show="round.answerSubmitted && round.correctAnswer">Correct!</p>
      <p v-show="round.answerSubmitted && !round.correctAnswer">
        Wrong! The correct answer is {{ round.answer }}
      </p>
      <p v-show="!round.answerSubmitted && round.timer == 0">
        You ran out of time! The answer is {{ round.answer }}
      </p>
      <button id="nextRoundBtn" class="button is-primary is-large is-rounded" v-show="round.roundComplete"
              @click="nextRound"
              type="button"
              name="button">
              Next Question
      </button>
    </div>
  </div>
</template>

<script>
import Round from '../utilities/round';

const axios = require('axios');

export default {
  data() {
    return {
      round: {},
    };
  },
  computed: {
    isDisabled() {
      return (!this.round.userAnswer || this.roundComplete);
    },
  },
  mounted() {
    axios.get('http://localhost:3000/play').then((response) => {
      const round = new Round(response.data);
      this.round = round;
    });

    window.setInterval(() => {
      if (this.round.timer > 0 && !this.round.answerSubmitted) {
        this.round.timer -= 1;
        this.round.checkTimer();
      }
    }, 1000);
  },
  methods: {
    nextRound() {
      axios.get('http://localhost:3000/play').then((response) => {
        this.round = new Round(response.data);
      });
    },
  },
};
</script>

<style lang="css">
  .play-round {
    margin-top: 60px;
    font-family: 'Montserrat', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    font-size: 30px;
  }
  .questionArea {
    margin-top: 40px;
  }
    input[type="text"]{
    text-align: center;
  }
  .timerArea {
    margin-top: 40px;
  }
  .answerArea {
    margin-top: 40px;
  }
  .resultArea {
    padding-top: 40px;
  }
  #nextRoundBtn {
    margin-top: 20px;
  }
</style>



